.barra {
  position: absolute;
  right: 40px;
  top: 10px;
  background-color: black;
  background: black;
  border-radius: 10px;
  padding: 10px;
  height: 55px;
  width: 295px;
}

.textobarra {
  position: absolute;
  text-align: center;
  font: normal 18pt "Jersey 10";
  top: 2px;
  left: 100px;
  color:white;
  text-shadow: 0px 0px 10px black;


}

.textobarra p{
margin: 0px;
  padding: 5px;
}



.echosed{
  height: 350px;
  border-radius: 20%;
  border: 5px solid white;
  transition: .5s;
  margin-left: auto;
  margin-top: 50px;
  transform: scaleX(-1)
}




.enemyvida{
  background: linear-gradient(to top, green, rgb(55, 208, 55));
  position: absolute;
  right: 3px;
  top: 3px;
  width: 300px;
  height: 25px;
  border-radius: 5px;
  border: 4px solid rgb(7, 96, 22);
  display: flex;
  justify-content: center;
  align-items: center;
}

.enemyvidamedia{
  background: linear-gradient(to top, rgb(128, 102, 0), rgb(234, 234, 44));
  position: absolute;
  right: 3px;
  top: 3px;
  width: 300px;
  height: 25px;
  border-radius: 5px;
  border: 4px solid rgb(96, 65, 7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.enemyvidafraca{
  background: linear-gradient(to top, rgb(128, 0, 0), rgb(234, 44, 44));
  position: absolute;
  right: 3px;
  top: 3px;
  width: 300px;
  height: 25px;
  border-radius: 5px;
  border: 4px solid rgb(96, 7, 7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.enemyvida p, .enemyenergia p{
  position: relative;
  color: white;
  font: bold 10pt arial;
  text-shadow: 0px 0px 10px black;
}


.enemyenergia{
  background: linear-gradient(to top, rgb(11, 0, 128), rgb(55, 93, 208));
  position: absolute;
  right: 3px;
  top: 38px;
  width: 300px;
  height: 25px;
  border-radius: 5px;
  border: 4px solid rgb(11, 0, 128);
  display: flex;
  justify-content: center;
  align-items: center;
}

