body {
  background-color: rgb(34, 34, 34);
}

.principal{
  background-color: black;
  margin: 0px auto;
  padding: 0px;
  width: 900px;
  border-radius: 20px;
}

.tela {
  background-size: 100%;
  background-position:center;
  background-repeat: no-repeat;
  width: 854px;
  height: 480px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  position: relative;
}


.titulo {
  background-color: black;
  color: yellow;
  font: normal 15pt "Jersey 10";
  text-shadow: 0px 0px 10px rgb(236, 132, 34);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 65px;
}

.legenda {
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.legenda p{
color:white;
font: normal 15pt "Jersey 10";
padding: 0px;
margin: 0px;}



.painelbotao{
  height: 100px;
  width: 100%;
  background-color: rgb(0, 0, 0);
  color: yellow;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}




footer{
  color: white;
  text-align: center;
  font: normal 10pt Arial;
}

.sprite{
  height: 50%;
  border-radius: 20%;
  border: 5px solid white;
  display: flex;
  justify-content:center;
  transition: .5s;
  margin: 0px 20px;
}

.sprite:hover{
  transform: scale(1.2);
  border: 10px solid yellow;
  box-shadow:0px 0px 10px rgb(255, 175, 54);
}

.turno{
  color: whitesmoke;
  font: normal 30pt "Jersey 10";
  text-shadow: 0px 0px 10px black;
  background-color: rgb(0, 0, 0, 0.5);
  padding: 5px 100px;
  border-radius: 20px;
}

.win{
  color: whitesmoke;
  font: normal 60pt "Jersey 10";
  text-shadow: 0px 0px 10px black;
  background-color: rgba(0, 173, 12, 0.5);
  padding: 5px 100px;
  border-radius: 20px;
}

.lose{
  color: whitesmoke;
  font: normal 60pt "Jersey 10";
  text-shadow: 0px 0px 10px black;
  background-color: rgba(207, 0, 0, 0.5);
  padding: 5px 100px;
  border-radius: 20px;
}
