

.barra {
  position: absolute;
  left: 40px;
  top: 10px;
  background-color: black;
  background: black;
  border-radius: 10px;
  padding: 10px;
  height: 55px;
  width: 295px;
}

.textobarra {
  font: normal 18pt "Jersey 10";
  position: absolute;
  text-align: center;
  top: 4px;
  left: 110px;
  color:white;
  text-shadow: 0px 0px 10px black;
}

.textobarra p{
  margin: 0px;
  padding: 5px;
}

.vida{
  position: relative;
  left: -71px;
  top: -11px;
}

.vida div{
  background: linear-gradient(to top, green, rgb(55, 208, 55));
  position: absolute;
  left: 65px;
  top: 5px;
  width: 300px;
  height: 25px;
  border-radius: 5px;
  border: 4px solid rgb(7, 96, 22);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s
}

.vida p {
  position: relative;
  color: white;
  font: bold 10pt arial;
  text-shadow: 0px 0px 10px black;
}


.energia{
  width: 300px;
  height: 20px;
  display: flex;
  justify-content: left;
  align-items: center;
}

.energia div{
  position: absolute;
  left: 3px;
  top: 38px;
  height: 25px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
}


.eon{
  background: linear-gradient(to top, rgb(23, 8, 188), rgb(0, 64, 255));
  border: 4px solid rgb(11, 0, 128);
  box-shadow: 0px 0px 5px rgb(0, 4, 255);
  transition: 0.5s
}

.eoff{
  background: linear-gradient(to top, rgb(11, 0, 128), rgb(55, 93, 208));
  border: 4px solid rgb(11, 0, 128);
  transition: 0.5s
}

.rageon{
  background: linear-gradient(to top, rgb(188, 8, 0), rgb(255, 0, 0));
  border: 4px solid rgb(128, 0, 0);
  box-shadow: 0px 0px 5px rgb(255, 0, 0);
  transition: 0.5s
}

.rageoff{
  background: linear-gradient(to top, rgb(105, 7, 2), rgb(228, 31, 31));
  border: 4px solid rgb(128, 0, 0);
}

.schosed{
  height: 350px;
  border-radius: 20%;
  border: 5px solid white;
  transition: .5s;
  margin-right: auto;
  margin-top: 50px;
}