.bton{
  border-radius: 20px;
  font: bold 2em "Jersey 10";
  background: linear-gradient(to top, #fbff00, #ffb700);
  border: 3px solid orange;
  color:rgb(0, 0, 0);
  transition: 0.5s;
  margin: 0px 20px;
  padding: 5px 15px;
}

.bton:hover {
    background: rgb(0, 0, 0); 
    color:rgb(250, 230, 0);
    border: 3px solid rgb(250, 230, 0);
    transform: scale(1.2);
}

.btoff{
  border-radius: 20px;
  font: bold 2em "Jersey 10";
  background: linear-gradient(to top, #919191, #e5e5e5);
  border: 3px solid rgb(92, 92, 92);
  color:rgb(0, 0, 0);
  margin: 0px 20px;
  padding: 5px 15px;
}

